import * as React from "react"
import { useState, useEffect } from "react"

function API() {
    const [request, setRequest] = useState(0)
    const [message, setMessage] = useState('loading....')


    useEffect(() => {
        try {
            let origin_url = window.location.origin
            fetch(`${origin_url}/api`)
            .then((res) => res.json())
            .then((data) => {
                setMessage("Server is up and running")
            })
            .catch((error) => {
                setMessage('Server down or not founnd')
            })
        } catch (error) {
            setMessage('Server down or not founnd')
        }
    }, [request]);


    return (
        <div>
            <h1>
                Status
            </h1>
            <button onClick={() => setRequest(request+1)}>Check Status</button>
            <br />
            <small>{message}</small>
        </div>
    )
}

export default API