import * as React from "react"
import { useState, useEffect } from "react"

function AddProduct() {
    const [request, setRequest] = useState(0)
    const [message, setMessage] = useState('editing....')
    const [product, setProduct] = useState(null)
    const [key, setkey] = useState(null)


    useEffect(() => {
        try {
            if (key){
                let origin_url = window.location.origin
                let url = new URL(`${origin_url}/api/delete_product`)
                fetch(url, {method: 'DELETE', headers:{'accept':'application/json', 'Content-Type':'application/json'}, body:JSON.stringify({'key':key})})
                .then((res) => res.json())
                .then((data) => {
                    console.log(data)
                    setMessage('Product Deleted')
                    setProduct(data.key)
                })
                .catch((error) => {
                    setMessage('Product was not able to be deleted, the server is most likley down')
                })
            } else {
                setMessage('you need to enter a key')
            }
        } catch (error) {
            setMessage('an error :(')
        }
    }, [request]);

    return (
        <div>
            <h1>
                Delete Product
            </h1>
            <label htmlFor="key">Key</label>
            <input type="text" id="key" onChange={(e) => setkey(e.target.value)}/>
            <br />
            <button onClick={() => setRequest(request+1)}>Delete Product</button>
            <br />
            <small>{message}</small>
            {
                product &&

                <div>
                    {product}
                </div>
            }
        </div>
    )
}

export default AddProduct