import * as React from "react"
import { useState, useEffect } from "react"

function AddProduct() {
    const [request, setRequest] = useState(0)
    const [message, setMessage] = useState('adding....')

    const [key, setKey] = useState(null)
    const [name, setName] = useState(null)
    const [price, setPrice] = useState(null)

    const [product, setProduct] = useState(null)


    useEffect(() => {
        try {
            if (name || price){
                let origin_url = window.location.origin
                let url = new URL(`${origin_url}/api/add_product`)
                fetch(url, {method:'POST', headers:{'accept':'application/json', 'Content-Type':'application/json'}, body: JSON.stringify({'name':name, 'price':price})})
                .then((res) => res.json())
                .then((data) => {
                    console.log(data)
                    setMessage('Product added!')
                    setKey(data.key)
                    setName(data.name)
                    setPrice(data.price)
                    setProduct(true)
                })
                .catch((error) => {
                    setMessage('Product not added :(')
                })
            } else {
                setMessage('you need to enter a name and/or price')
            }
        } catch (error) {
            setMessage('an error :(')
        }
    }, [request]);

    return (
        <div>
            <h1>
                Add Product
            </h1>
            <label htmlFor="name">Product Name</label>
            <input type="text" id="name" onChange={(e) => setName(e.target.value)}/>
            <br />
            <label htmlFor="price">Product Price</label>
            <input type="text" id="price" onChange={(e) => setPrice(e.target.value)}/>
            <br />
            <button onClick={() => setRequest(request+1)}>Add Product</button>
            <br />
            <small>{message}</small>
            {
                product &&

                <div>
                    <div>{key}</div>
                    <div>{name}</div>
                    <div>{price}</div>
                </div>
            }
        </div>
    )
}

export default AddProduct