import * as React from "react"
import { useState, useEffect } from "react"

function AddProduct() {
    const [request, setRequest] = useState(0)
    const [message, setMessage] = useState('editing....')
    const [name, setName] = useState(null)
    const [price, setPrice] = useState(null)
    const [product, setProduct] = useState(null)
    const [key, setkey] = useState(null)


    useEffect(() => {
        try {
            if (key){
                let origin_url = window.location.origin
                let url = new URL(`${origin_url}/api/edit_product`)
                fetch(url, {method:'PUT', headers:{'accept':'application/json', 'Content-Type':'application/json'}, body:JSON.stringify({'key':key, 'name':name, 'price':price})})
                .then((res) => res.json())
                .then((data) => {
                    console.log(data)
                    setMessage('Product edited!')
                    setProduct(data.key)
                })
                .catch((error) => {
                    setMessage('Product not edited :(')
                })
            } else {
                setMessage('you need to enter a key')
            }
        } catch (error) {
            setMessage('an error :(')
        }
    }, [request]);

    return (
        <div>
            <h1>
                Edit Product
            </h1>
            <label htmlFor="key">Key</label>
            <input type="text" id="key" onChange={(e) => setkey(e.target.value)}/>
            <br />
            <label htmlFor="name">Product Name</label>
            <input type="text" id="name" onChange={(e) => setName(e.target.value)}/>
            <br />
            <label htmlFor="price">Product Price</label>
            <input type="text" id="price" onChange={(e) => setPrice(e.target.value)}/>
            <br />
            <button onClick={() => setRequest(request+1)}>Add Product</button>
            <br />
            <small>{message}</small>
            {
                product &&

                <div>
                    <div>{product}</div>
                </div>
            }
        </div>
    )
}

export default AddProduct